//   
export function debounce(func: (event:Event) => void, delay: number) {
    //      
    let timerId: NodeJS.Timeout | null;
    //   ,      func
    return function (event:Event) {
        //   ,   
        if (timerId) {
            clearTimeout(timerId);
        }
        //   ,    func  delay 
        timerId = setTimeout(() => {
            func(event);
        }, delay);
    };
}

